.app-container {
  background: rgba(0, 0, 0, 0.7) url('background.jpg') no-repeat center center fixed;
  background-size: cover;
  min-height: 100vh;
}

.nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.8);
}

.nav-link {
  color: #FBF5DD;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  text-shadow: 0 0 10px #DDA853;
}

.nav-link:hover {
  color: #DDA853;
  text-shadow: 0 0 15px #DDA853;
}

.bonanza-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 15px;
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  position: relative;
}

.wins-ticker {
  background: rgba(0, 0, 0, 0.9);
  padding: 8px;
  text-align: center;
  font-size: 14px;
  color: #DDA853;
  text-shadow: 0 0 10px #DDA853;
  margin-bottom: 15px;
  border: 2px dashed #16404D;
  overflow-x: auto;
  white-space: nowrap;
}

.win-item {
  margin: 0 15px;
  display: inline-block;
  animation: tickerGlow 2s infinite;
}

@keyframes tickerGlow {
  0% { text-shadow: 0 0 10px #DDA853; }
  50% { text-shadow: 0 0 20px #16404D; }
  100% { text-shadow: 0 0 10px #DDA853; }
}

.title {
  text-align: center;
  color: #FBF5DD;
  font-size: 36px;
  text-shadow: 0 0 20px #DDA853, 0 0 40px #16404D;
  animation: neonPulse 1.5s infinite;
}

@keyframes neonPulse {
  0% { text-shadow: 0 0 20px #DDA853, 0 0 40px #16404D; }
  50% { text-shadow: 0 0 30px #DDA853, 0 0 60px #16404D; }
  100% { text-shadow: 0 0 20px #DDA853, 0 0 40px #16404D; }
}

.user-panel {
  text-align: center;
  margin: 15px 0;
  padding: 10px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  font-size: 20px;
  text-transform: uppercase;
  border: 3px solid #DDA853;
  box-shadow: 0 0 15px #DDA853;
}

.cabinet-link {
  color: #FBF5DD;
  text-decoration: none;
  margin-left: 10px;
  text-shadow: 0 0 10px #DDA853;
}

.cabinet-link:hover {
  color: #16404D;
  text-shadow: 0 0 15px #16404D;
}

.slots-container {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 20px 0;
  flex-wrap: wrap;
}

.slot-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slot {
  width: 80px;
  height: 80px;
  background: linear-gradient(45deg, #FBF5DD, #16404D);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  font-weight: bold;
  color: #DDA853;
  border: 4px solid #DDA853;
  box-shadow: 0 0 20px #DDA853;
  position: relative;
  overflow: hidden;
  perspective: 1000px;
}

.slot.spinning {
  animation: spinDrum 0.2s linear infinite;
}

@keyframes spinDrum {
  0% { transform: rotateX(0deg); }
  100% { transform: rotateX(360deg); }
}

.slot-label {
  margin-top: 5px;
  font-size: 12px;
  color: #FBF5DD;
  text-shadow: 0 0 10px #DDA853;
}

.sum-display {
  text-align: center;
  margin: 20px 0;
  padding: 15px;
  font-size: 20px;
  text-transform: uppercase;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  border: 5px solid #FBF5DD;
  box-shadow: 0 0 40px #FBF5DD, inset 0 0 20px #DDA853;
  animation: borderPulse 2s infinite;
}

@keyframes borderPulse {
  0% { box-shadow: 0 0 40px #FBF5DD; }
  50% { box-shadow: 0 0 50px #DDA853; }
  100% { box-shadow: 0 0 40px #FBF5DD; }
}

.red { color: #ff3366; text-shadow: 0 0 15px #ff3366; }
.yellow { color: #ffff66; text-shadow: 0 0 15px #ffff66; }
.cyan { color: #66ccff; text-shadow: 0 0 15px #66ccff; }
.green { color: #00ff9d; text-shadow: 0 0 15px #00ff9d; }
.gold { color: #ffd700; text-shadow: 0 0 20px #ffd700; animation: jackpotGlow 1s infinite; }

@keyframes jackpotGlow {
  0% { text-shadow: 0 0 20px #ffd700; }
  50% { text-shadow: 0 0 30px #ffa500; }
  100% { text-shadow: 0 0 20px #ffd700; }
}

.controls {
  display: flex;
  justify-content: center;
  gap: 15px;
  align-items: center;
  margin: 20px 0;
  flex-wrap: wrap;
}

.bet-section {
  display: flex;
  align-items: center;
  gap: 10px;
}

.bet-input {
  padding: 8px;
  border-radius: 8px;
  border: 2px solid #DDA853;
  width: 80px;
  background: #16404D;
  color: #FBF5DD;
  font-size: 16px;
  text-align: center;
}

.spin-button {
  padding: 10px 20px;
  background: linear-gradient(45deg, #FBF5DD, #16404D);
  border: 2px solid #DDA853;
  border-radius: 10px;
  color: #DDA853;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
  box-shadow: 0 0 15px #DDA853;
  transition: all 0.3s;
}

.spin-button:hover:not(:disabled) {
  transform: scale(1.1);
  box-shadow: 0 0 20px #DDA853;
}

.spin-button:disabled {
  background: #666;
  border-color: #333;
  cursor: not-allowed;
  box-shadow: none;
}

.result {
  text-align: center;
  margin-top: 20px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  font-size: 18px;
  border: 5px solid #FBF5DD;
  box-shadow: 0 0 40px #FBF5DD, inset 0 0 20px #DDA853;
  animation: borderPulse 2s infinite;
}

.win {
  color: #00ff9d;
  text-shadow: 0 0 15px #00ff9d;
}

.lose {
  color: #ff3366;
  text-shadow: 0 0 15px #ff3366;
}

.ad-banner {
  position: fixed;
  width: 150px;
  padding: 15px;
  background: #16404D;
  color: #DDA853;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid #DDA853;
  box-shadow: 0 0 20px #DDA853;
  text-transform: uppercase;
  z-index: 10;
}

.left-ad {
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.right-ad {
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

/* Медиа-запросы для мобильных устройств */
@media (max-width: 768px) {
  .bonanza-container {
    padding: 10px;
  }

  .wins-ticker {
    font-size: 12px;
    padding: 5px;
  }

  .title {
    font-size: 28px;
  }

  .user-panel {
    font-size: 16px;
    padding: 8px;
  }

  .slots-container {
    gap: 5px;
    margin: 15px 0;
  }

  .slot {
    width: 60px;
    height: 60px;
    font-size: 28px;
    border: 3px solid #DDA853;
  }

  .slot-label {
    font-size: 10px;
  }

  .sum-display {
    font-size: 16px;
    margin: 15px 0;
    padding: 10px;
    box-shadow: 0 0 30px #FBF5DD, inset 0 0 15px #DDA853;
  }

  .controls {
    gap: 10px;
    flex-direction: column;
  }

  .bet-input {
    width: 70px;
    font-size: 14px;
    padding: 6px;
  }

  .spin-button {
    padding: 8px 16px;
    font-size: 16px;
  }

  .result {
    font-size: 14px;
    padding: 10px;
    box-shadow: 0 0 30px #FBF5DD, inset 0 0 15px #DDA853;
  }

  .ad-banner {
    width: 100px;
    font-size: 12px;
    padding: 10px;
  }

  .left-ad {
    left: 5px;
  }

  .right-ad {
    right: 5px;
  }
}

@media (max-width: 480px) {
  .nav {
    gap: 10px;
    padding: 10px;
  }

  .nav-link {
    font-size: 16px;
  }

  .title {
    font-size: 24px;
  }

  .user-panel {
    font-size: 14px;
  }

  .slot {
    width: 50px;
    height: 50px;
    font-size: 24px;
  }

  .slot-label {
    font-size: 8px;
  }

  .sum-display {
    font-size: 14px;
    padding: 8px;
    box-shadow: 0 0 25px #FBF5DD, inset 0 0 10px #DDA853;
  }

  .bet-input {
    width: 60px;
    font-size: 12px;
  }

  .spin-button {
    padding: 6px 12px;
    font-size: 14px;
  }

  .result {
    font-size: 14px;
    padding: 8px;
    box-shadow: 0 0 25px #FBF5DD, inset 0 0 10px #DDA853;
  }

  .ad-banner {
    width: 80px;
    font-size: 10px;
    padding: 8px;
  }
}

.slot.spinning {
  animation: spinDrum 0.2s linear infinite;
}
.slot.spinning::before {
  content: attr(data-number);
  animation: numberFade 0.1s infinite;
}
@keyframes numberFade {
  0% { opacity: 0.5; }
  50% { opacity: 1; }
  100% { opacity: 0.5; }
}
.history {
  margin-top: 20px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  border: 3px solid #DDA853;
  box-shadow: 0 0 20px #DDA853, inset 0 0 10px #16404D;
  text-align: center;
}

.history h3 {
  font-size: 18px;
  margin-bottom: 10px;
  color: #FBF5DD;
  text-shadow: 0 0 10px #DDA853;
}

.history p {
  margin: 5px 0;
  font-size: 14px;
}